import * as Yup from 'yup';
import { LoadingButton, LocalizationProvider, MobileDatePicker } from '@mui/lab';
import DateAdapter from '@mui/lab/AdapterDateFns';
import { motion } from 'framer-motion';
import { Link as RouterLink } from 'react-router-dom';
import { useLocation } from 'react-router-dom';
import { MotionContainer, varBounceIn } from '../components/animate';
import {
  Box,
  Button,
  Card,
  CardContent,
  CardHeader,
  Container,
  Divider,
  Modal,
  Stack,
  TextField,
  Typography
} from '@mui/material';
import { styled } from '@mui/material/styles';
import { Form, FormikProvider, useFormik } from 'formik';
import { useState, useEffect } from 'react';
import axios from 'axios';
import Web3 from 'web3';
import getAddressFrom from '../utils/AddressExtractor';
import sendTransaction from '../utils/TxSender';
import COMMON_HEADER from '../common/HeaderType';
import COMMON_ABI from '../common/ABI';
import { onResponse, onContractCall } from '../common/ErrorMessage';
import moment from 'moment';
import Page from '../components/Page';

//  
const ImgStyle = styled('img')({
  top: 0,
  width: '100%',
  height: '100%',
  objectFit: 'contain',
  position: 'absolute'
});

/**
 * [  ]
 */
const SaleRegistration = () => {
  // []  ID (URL  )
  const tokenId = useLocation().pathname.substring(15);

  // [] , , ,  , ,  ,  ,  ,  ,
  //        ( ,  ),   ,   
  const [item, setItem] = useState('');
  const symbol = 'SSF';
  const [author, setAuthor] = useState('');
  const [authorAddr, setAuthorAdr] = useState('');
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [date, setDate] = useState(new Date().toLocaleDateString('ko-KR'));
  const [price, setPrice] = useState('0');
  const [minPrice, setMinPrice] = useState('0');
  const [current, setCurrent] = useState('');
  const [due, setDue] = useState('');
  const [isSale, setIsSale] = useState(false);
  const [isComplete, setIsComplete] = useState(false);

  // []    (, ,  )
  const [approveModal, setApproveModal] = useState(false);
  const [privKey, setPrivKey] = useState('');
  const [loading, setLoading] = useState(false);

  // Web3
  const web3 = new Web3(new Web3.providers.HttpProvider(process.env.REACT_APP_ETHEREUM_RPC_URL));


  /**
   * [  ]
   *      .
   */
  useEffect(() => {
    checkInfo();
  }, []);

  //  
  const typeSchema = Yup.object().shape({
    price: Yup.number().positive().integer().required(),
    minPrice: Yup.number().positive().integer().required()
  });

  //   
  const formik = useFormik({
    initialValues: {
      price: '',
      minPrice: ''
    },
    validationSchema: typeSchema,
    onSubmit: (value) => {
      setPrice(value.price);
      setMinPrice(value.minPrice);
    }
  });
  const { errors, touched, handleSubmit, handleReset, getFieldProps } = formik;

  //    
  const handleDate = (value) => {
    setDate(value);
  };

  //   ( )
  const toggleApprove = () => {
    setApproveModal(!approveModal);
  };

  //   
  const handlePrivKey = (e) => {
    setPrivKey(e.target.value);
  };

  /**
   * [  ]
   *             .
   *      (0) .
   *        . 
   */
  const checkDate = () => {
    const now = parseInt((moment() / 1000).toFixed(0));
    const endDate = parseInt((moment(date) / 1000).toFixed(0));

    if (now > endDate) alert('     .');
    else {
      setCurrent(now);
      setDue(endDate);
      toggleApprove();
    }
  };


  /**
   * PJT  -  2:  
   * Req. 2-F3   
   * 
   * token id    .
   *  tokenURI NFT  .
   * 
   * PJT  -  2:    
   * Req. 2-F3    
   * 
   *      sale      
   * (, )  . 
   * 
   */
  const checkInfo = async () => {
    // TODO
    setIsSale(false);
  };

  /**
   * PJT  -  2:    
   * Req. 2-F2 Sale   
   * 
   * 1.       getAddressFrom()     .
   * 2.    Sale Factory  createSale()    Sale  .
   * 3.      Sale    .
   * 4.  Sale    NFT     Sale NFT  (transferFrom())
   * 5.      API    . 
   * 
   */
  const createSaleContract = async () => {
    // TODO     
    setLoading(false);
  };

  /**
   * PJT  -  2:    
   * Req. 2-F1    
   * Req. 2-F2 Sale   
   * 
   *  createSaleContract()
   *       API  .
   */
  const registerSaleInfo = async (pubKey, saleCA) => {
    setLoading(false);
  };

  return (
    <Page title="SSAFY NFT" maxWidth="100%" minHeight="100%">
      {isComplete === false ? (
        <>
          {isSale === true ? (
            <Container sx={{ pt: 3, pl: 10, pr: 10 }}>
              <Typography variant="h3" sx={{ pb: 3, pr: 10 }}>
                  .
              </Typography>

              <Stack direction="row" sx={{ mt: 4 }}>
                <Stack width="30%">
                  <Card>
                    <Box sx={{ pt: '100%', position: 'relative' }}>
                      <ImgStyle src={item.length !== 0 ? item : null} />
                    </Box>
                  </Card>
                </Stack>

                <Stack sx={{ ml: 10 }} width="70%">
                  <Card>
                    <CardHeader sx={{ ml: 1, mb: 2 }} title=" " />
                    <Divider />
                    <CardContent sx={{ ml: 1, mr: 1, pt: 4 }}>
                      <Stack direction="row">
                        <Typography sx={{ fontSize: 18 }}>  : </Typography>
                        <Typography sx={{ ml: 1 }} variant="h6">
                          {author}
                        </Typography>
                      </Stack>
                      <Stack direction="row" sx={{ mt: 3 }}>
                        <Typography sx={{ fontSize: 18 }}>  : </Typography>
                        <Typography sx={{ ml: 1 }} variant="h6">
                          {title}
                        </Typography>
                      </Stack>
                      <Stack direction="row" sx={{ mt: 3 }}>
                        <Typography sx={{ fontSize: 18 }}>   : </Typography>
                        <Typography sx={{ ml: 1 }} variant="h6">
                          {description}
                        </Typography>
                      </Stack>
                    </CardContent>
                  </Card>
                </Stack>
              </Stack>

              <Stack sx={{ mb: 7 }}></Stack>
              <FormikProvider value={formik}>
                <Form autoComplete="off" noValidate onSubmit={handleSubmit} onReset={handleReset}>
                  <Stack spacing={3}>
                    <Stack direction="row" alignItems="center">
                      <TextField
                        sx={{ width: '100%' }}
                        type="number"
                        label=" "
                        {...getFieldProps('price')}
                        error={Boolean(touched.price && errors.price)}
                      />
                      <Typography variant="h5" sx={{ pl: 2 }}>
                        {symbol}
                      </Typography>
                    </Stack>
                    <Stack direction="row" alignItems="center">
                      <TextField
                        sx={{ width: '100%' }}
                        type="number"
                        label=" "
                        {...getFieldProps('minPrice')}
                        error={Boolean(touched.minPrice && errors.minPrice)}
                      />
                      <Typography variant="h5" sx={{ pl: 2 }}>
                        {symbol}
                      </Typography>
                    </Stack>
                    <LocalizationProvider dateAdapter={DateAdapter}>
                      <Stack sx={{ pt: 1 }}>
                        <MobileDatePicker
                          label=" "
                          inputFormat="yyyy-MM-dd"
                          value={date}
                          disablePast
                          onChange={handleDate}
                          renderInput={(params) => <TextField sx={{ width: '100%' }} {...params} />}
                        />
                      </Stack>
                    </LocalizationProvider>
                  </Stack>

                  <Divider sx={{ mt: 5, width: '100%' }} />

                  <Stack alignItems="flex-end">
                    <Button
                      sx={{ mt: 5, width: '40%', fontSize: 18 }}
                      size="large"
                      type="submit"
                      variant="contained"
                      onClick={
                        Object.keys(touched).length &&
                        !Object.keys(errors).length &&
                        item.length !== 0
                          ? checkDate
                          : null
                      }
                    >
                       
                    </Button>

                    <Modal open={approveModal}>
                      <Card
                        sx={{
                          width: '40%',
                          border: 1,
                          borderRadius: 1,
                          borderColor: 'grey.main',
                          backgroundColor: '#ffffff',
                          top: '25%',
                          left: '30%'
                        }}
                      >
                        <Box>
                          <Typography sx={{ ml: 3, pt: 2 }} variant="h5">
                             
                          </Typography>
                        </Box>
                        <Divider sx={{ mt: 2 }} />
                        <FormikProvider value={formik}>
                          <Form autoComplete="off" noValidate onSubmit={handleSubmit} onReset={handleReset}>
                            <Box sx={{ mt: 4, ml: 3, mr: 3 }}>
                              <Stack direction="row" sx={{ mt: 3 }}>
                                <TextField
                                  fullWidth
                                  type="text"
                                  label=""
                                  onChange={handlePrivKey}
                                />
                              </Stack>
                              <Divider sx={{ mt: 4 }} />
                              <Stack direction="row" sx={{ mt: 3, mb: 3 }} justifyContent="center">
                                {loading === false ? (
                                  <Button
                                    size="large"
                                    variant="contained"
                                    sx={{ width: '20%', fontSize: 16 }}
                                    onClick={toggleApprove}
                                  >
                                    
                                  </Button>
                                ) : null}
                                <LoadingButton
                                  size="large"
                                  variant="contained"
                                  sx={{ ml: 3, width: '20%', fontSize: 16 }}
                                  loading={loading}
                                  onClick={createSaleContract}
                                >
                                  
                                </LoadingButton>
                              </Stack>
                            </Box>
                          </Form>
                        </FormikProvider>
                      </Card>
                    </Modal>
                  </Stack>
                </Form>
              </FormikProvider>
            </Container>
          ) : (
            <Container>
              <MotionContainer initial="initial" sx={{ mt: 10 }} open>
                <Box sx={{ maxWidth: 480, margin: 'auto', textAlign: 'center' }}>
                  <motion.div variants={varBounceIn}>
                    <Typography variant="h3" paragraph>
                       .
                    </Typography>
                  </motion.div>

                  <Typography sx={{ color: 'text.secondary' }}>
                           .
                  </Typography>

                  <motion.div variants={varBounceIn}>
                    <Box
                      component="img"
                      src="/static/illustrations/illustration_register.png"
                      sx={{ height: 260, mx: 'auto', my: { xs: 5, sm: 10 } }}
                    />
                  </motion.div>
                </Box>
              </MotionContainer>
            </Container>
          )}
        </>
      ) : (
        <Container>
          <MotionContainer initial="initial" sx={{ mt: 10 }} open>
            <Box sx={{ maxWidth: 480, margin: 'auto', textAlign: 'center' }}>
              <motion.div variants={varBounceIn}>
                <Typography variant="h3" paragraph>
                    
                </Typography>
              </motion.div>
              <Typography sx={{ color: 'text.secondary' }}>  .</Typography>

              <motion.div variants={varBounceIn}>
                <Box
                  component="img"
                  src="/static/illustrations/illustration_register.png"
                  sx={{ height: 260, mx: 'auto', my: { xs: 5, sm: 10 } }}
                />
              </motion.div>

              <Button to="/" size="large" variant="contained" component={RouterLink}>
                 
              </Button>
            </Box>
          </MotionContainer>
        </Container>
      )}
    </Page>
  );
};

export default SaleRegistration;
