import * as Yup from 'yup';
import { motion } from 'framer-motion';
import { LoadingButton } from '@mui/lab';
import { MotionContainer, varBounceIn } from '../components/animate';
import {
  Box,
  Button,
  Card,
  Container,
  Divider,
  Modal,
  Stack,
  TextField,
  Typography
} from '@mui/material';
import { Form, FormikProvider, useFormik } from 'formik';
import { useState, useRef } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import axios from 'axios';
import Web3 from 'web3';
import COMMON_ABI from '../common/ABI';
import COMMON_HEADER from '../common/HeaderType';
import { onResponse, onContractCall } from '../common/ErrorMessage';
import getAddressFrom from '../utils/AddressExtractor';
import sendTransaction from '../utils/TxSender';
import Page from '../components/Page';

/**
 * []  UI 
 */
const ItemRegistration = () => {
  // []  (, , input  ), , ,  ,  ID
  const [item, setItem] = useState('');
  const [itemName, setItemName] = useState('');
  const itemSelect = useRef();
  const [author, setAuthor] = useState('');
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [tokenId, setTokenId] = useState('');

  // []    (, ,  ,  )
  const [approveModal, setApproveModal] = useState(false);
  const [privKey, setPrivKey] = useState('');
  const [loading, setLoading] = useState(false);
  const [isComplete, setIsComplete] = useState(false);

  // Web3
  const web3 = new Web3(new Web3.providers.HttpProvider(process.env.REACT_APP_ETHEREUM_RPC_URL));

  //  
  const typeSchema = Yup.object().shape({
    author: Yup.string().required(' .'),
    title: Yup.string().required(' .')
  });

  //   
  const formik = useFormik({
    initialValues: {
      author: '',
      title: '',
      description: ''
    },
    validationSchema: typeSchema,
    onSubmit: (value) => {
      setAuthor(value.author);
      setTitle(value.title);
      setDescription(value.description);
    }
  });
  const { errors, touched, handleSubmit, handleReset, getFieldProps } = formik;

  //    
  const handleClick = () => {
    itemSelect.current.click();
  };

  //   
  const handleItem = (value) => {
    setItem(value);

    if (value !== '') setItemName(value.name);
    else setItemName('');
  };

  //   ( )
  const toggleApprove = () => {
    setApproveModal(!approveModal);
  };

  //   
  const handlePrivKey = (e) => {
    setPrivKey(e.target.value);
  };

  /**
   * PJT  -  1:    NFT  
   * Req.1-F1       
   * 
   *  )
   * 1.           .
   * 2.      getAddressFrom()    ,    , ,
   *     formData    append .
   * 3.  formData   API  ,      item ID  .
   * 4.    item ID,    NFT    .
   *     token Id .
   * 5.    token Id owner_address   .  
   */
  const addItem = async () => {
    // TODO
  };

  return (
    <Page title="SSAFY NFT" maxWidth="100%" minHeight="100%">
      {isComplete === false ? (
        <Container sx={{ pt: 3, pl: 10, pr: 10 }}>
          <Typography variant="h3" sx={{ pb: 6, pr: 10 }}>
              .
          </Typography>

          <FormikProvider value={formik}>
            <Form autoComplete="off" noValidate onSubmit={handleSubmit} onReset={handleReset}>
              <Stack spacing={3}>
                <Stack direction="row" alignItems="center">
                  <input
                    type="file"
                    accept="image/png, image/jpeg, image/jpg, image/gif"
                    ref={itemSelect}
                    onChange={(e) =>
                      e.target.files.length !== 0 ? handleItem(e.target.files[0]) : handleItem('')
                    }
                    style={{ display: 'none' }}
                  />
                  <TextField
                    sx={{ width: '60%' }}
                    type="text"
                    label=" (  : png, jpeg, jpg, gif)"
                    value={itemName}
                    disabled
                  />
                  <Button
                    sx={{ ml: 5, fontSize: 18 }}
                    variant="contained"
                    size="large"
                    onClick={handleClick}
                  >
                    
                  </Button>
                </Stack>
                <TextField
                  sx={{ width: '70%' }}
                  inputProps={{ maxLength: 20 }}
                  type="text"
                  label=""
                  {...getFieldProps('author')}
                  error={Boolean(touched.author && errors.author)}
                  helperText={touched.author && errors.author}
                />
                <TextField
                  sx={{ width: '70%' }}
                  inputProps={{ maxLength: 140 }}
                  type="text"
                  label=""
                  {...getFieldProps('title')}
                  error={Boolean(touched.title && errors.title)}
                  helperText={touched.title && errors.title}
                />
                <TextField
                  sx={{ width: '70%' }}
                  inputProps={{ maxLength: 20 }}
                  type="text"
                  multiline
                  label="  (Option)"
                  {...getFieldProps('description')}
                />
              </Stack>

              <Divider sx={{ mt: 5, width: '70%' }} />

              <Stack alignItems="center">
                <Button
                  sx={{ mt: 5, width: '40%', fontSize: 18 }}
                  size="large"
                  type="submit"
                  variant="contained"
                  onClick={
                    Object.keys(touched).length && !Object.keys(errors).length && item.length !== 0
                      ? toggleApprove
                      : null
                  }
                >
                  
                </Button>

                <Modal open={approveModal}>
                  <Card
                    sx={{
                      width: '40%',
                      border: 1,
                      borderRadius: 1,
                      borderColor: 'grey.main',
                      backgroundColor: '#ffffff',
                      top: '25%',
                      left: '30%'
                    }}
                  >
                    <Box>
                      <Typography sx={{ ml: 3, pt: 2 }} variant="h5">
                         
                      </Typography>
                    </Box>
                    <Divider sx={{ mt: 2 }} />
                    <FormikProvider value={formik}>
                      <Form
                        autoComplete="off"
                        noValidate
                        onSubmit={handleSubmit}
                        onReset={handleReset}
                      >
                        <Box sx={{ mt: 4, ml: 3, mr: 3 }}>
                          <Stack direction="row" sx={{ mt: 3 }}>
                            <TextField
                              fullWidth
                              type="text"
                              label=""
                              onChange={handlePrivKey}
                              value={privKey}
                            />
                          </Stack>
                          <Divider sx={{ mt: 4 }} />
                          <Stack direction="row" sx={{ mt: 3, mb: 3 }} justifyContent="center">
                            {loading === false ? (
                              <Button
                                size="large"
                                variant="contained"
                                sx={{ width: '20%', fontSize: 16 }}
                                onClick={toggleApprove}
                              >
                                
                              </Button>
                            ) : null}
                            <LoadingButton
                              size="large"
                              variant="contained"
                              sx={{ ml: 3, width: '20%', fontSize: 16 }}
                              loading={loading}
                              onClick={addItem}
                            >
                              
                            </LoadingButton>
                          </Stack>
                        </Box>
                      </Form>
                    </FormikProvider>
                  </Card>
                </Modal>
              </Stack>
            </Form>
          </FormikProvider>
        </Container>
      ) : (
        <Container>
          <MotionContainer initial="initial" sx={{ mt: 5 }} open>
            <Box sx={{ maxWidth: 480, margin: 'auto', textAlign: 'center' }}>
              <motion.div variants={varBounceIn}>
                <Typography variant="h3" paragraph>
                    .
                </Typography>
              </motion.div>
              <Typography sx={{ color: 'text.secondary' }}>
                      .
              </Typography>

              <motion.div variants={varBounceIn}>
                <Box
                  component="img"
                  src="/static/illustrations/illustration_login.png"
                  sx={{ height: 260, mx: 'auto', my: { xs: 5, sm: 10 } }}
                />
              </motion.div>

              <Stack direction="row" justifyContent="space-between" sx={{ ml: 5, mr: 5 }}>
                <Button
                  to="/"
                  size="large"
                  color="inherit"
                  variant="contained"
                  component={RouterLink}
                  sx={{ fontSize: 16 }}
                >
                  
                </Button>
                <Button
                  to={`/register/sale/${tokenId}`}
                  size="large"
                  variant="contained"
                  component={RouterLink}
                >
                   
                </Button>
              </Stack>
            </Box>
          </MotionContainer>
        </Container>
      )}
    </Page>
  );
};

export default ItemRegistration;
